@use "../abstracts/" as *;

.features {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__card {
        margin-bottom: 25px;
        max-width: 311px;
        box-shadow: 0 15px 30px -11px hsla(213, 47%, 67%, 50%);
        border-radius: 8px;
        border-top: 3.55px solid $cyan;
    }

    &__cardTitle {
        font-weight: $semi-bold;
    }

    &__cardText {
        font-weight: $regular;
        font-size: 23px;
    }
}
